	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Это упражнение направлено на ту же группу мышц, что и жим штанги лежа. Но отличается от указанного упражнения тем, что жим гантелей лежа выполняется с большей амплитудой, что способствует большему растягиванию и сокращению грудных мышц, задействует большее число мышц-стабилизаторов.</p>\n<p>Если вы хотите правильно развивать грудные мышцы и увеличивать силовые показатели, то вы должны регулярно выполнять жимы гантелями.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n<li>Возьмите гантели в каждую руку, сядьте с ними на горизонтальную силовую скамью. Положите гантели на бедра и после этого лягте на скамью.</li>\n<li>Исходное положение: спина прогнута в пояснице, стопы ног упираются в пол. Руки с гантелями находятся на уровне плечей, грифы гантелей расположены на одной линии.</li>\n<li>На выдохе – вернитесь в исходное положение, максимально растягивая грудь.</li>\n<li>Проделайте необходимое количество повторений.</li>\n<li>Повторите упражнение нужное число раз.</li>\n</ol>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/P0JQiVi4y3E\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/tyaga-verhnego-bloka","efficiency":"7","title":"Тяга верхнего блока","image":"/static/2-00-0.jpg","image_2":"","image_3":"","image_4":"","filterby":"back","date":"April 12, 2017","injury":"4","bodypart":"Спина","filtertype":"isolated","type":"Изолирующее","filterinventory":"blocktrenajer","inventory":"Блоки","filterplace":null}}},"pathContext":{}}

